<template>
  <div class="personal-page">
    <div class="group1 flex-row justify-center">
      <div class="mid">
        <div class="name">
          <img :src="userList[activeIndex].nameurl" alt="" />
        </div>
        <div class="person flex-col justify-end align-center pr">
          <div class="role flex-col justify-end align-center">
            <img :src="userList[activeIndex].url" alt="" />
            <!-- <img src="../assets/set_user/user1@2x.png" alt="" /> -->
          </div>
          <div class="pan justify-center">
            <img src="../assets/set_user/pan@2x.png" alt="" />
          </div>
        </div>
      </div>
    </div>
    <div class="bottom-section">
      <div class="head-box">
        <div
          :class="['head-item', 2 === index ? 'active' : '']"
          v-for="(item, index) in sortedHeadList"
          :key="index"
        >
          <img :src="item.url" class="head" />
          <div class="text">{{ item.text }}</div>
        </div>
      </div>
      <div class="welcome-box">
        <div class="hd">
          <img
            class="logo"
            src="@/assets/images/personalPage/logo@2x.png"
            alt=""
          />
        </div>
        <div class="bd">
          <div class="t">与好友一同体验元宇宙</div>
          <div class="b">赢数字藏品 赚星钻</div>
        </div>
        <div class="ft">
          <img
            class="qrcode"
            src="@/assets/images/personalPage/qrcode@2x.png"
            alt=""
          />
        </div>
      </div>
      <div class="btn-box">
        <div class="btn">
          <img
            @click="share"
            src="@/assets/images/personalPage/btn_go@2x.png"
            alt=""
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import html2canvas from 'html2canvas'
export default {
  name: 'personalPage',
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      headList: [
        {
          text: '黑金',
          url: './static/img/user/thead1@2x.png'
        },
        {
          text: '钻石',
          url: './static/img/user/thead2@2x.png'
        },
        {
          text: '黑钻',
          url: './static/img/user/thead3@2x.png'
        },
        {
          text: '黄金',
          url: './static/img/user/thead4@2x.png'
        },
        {
          text: '铂金',
          url: './static/img/user/thead5@2x.png'
        }
      ],
      // 1-李白，2-李清照，3-信小福，4-关羽，5-花木兰
      userList: [
        {
          url: './static/img/user/guser1@2x.png',
          nameurl: './static/img/user/name1@2x.png'
        },
        {
          url: './static/img/user/guser2@2x.png',
          nameurl: './static/img/user/name2@2x.png'
        },
        {
          url: './static/img/user/guser3@2x.png',
          nameurl: './static/img/user/name3@2x.png'
        },
        {
          url: './static/img/user/guser4@2x.png',
          nameurl: './static/img/user/name4@2x.png'
        },
        {
          url: './static/img/user/guser5@2x.png',
          nameurl: './static/img/user/name5@2x.png'
        }
      ]
    }
  },
  computed: {
    activeIndex () {
      return this.userInfo.role - 1
    },
    sortedHeadList () {
      const cloneHeadList = [...this.headList]
      cloneHeadList[2] = this.headList[this.activeIndex]
      cloneHeadList[this.activeIndex] = this.headList[2]
      return cloneHeadList
    }
  },
  methods: {
    share () {
      html2canvas(document.body).then(function (canvas) {
        // document.body.appendChild(canvas)
        let imgurl = canvas.toDataURL('image/png') // 或者jpeg,听说jpeg性能更好，
        let a = document.createElement('a') // 新建a标签
        a.href = imgurl // 修改属性，url是一段base64
        a.download = '信纪元.png'
        a.click()
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import url('../assets/css/mixin.less');
.personal-page {
  position: relative;
  height: 100vh;
  background-image: url(~@/assets/images/personalPage/personal_page_bg@2x.png);
  background-position: center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  .group1 {
    width: 100%;
    height: calc(100% - 524px);

    .left {
      width: 132px;
      height: 100%;
      padding: 0 0 182px 44px;
    }
    .mid {
      width: calc(100% - 264px);
      height: 100%;
      .person {
        width: 100%;
        height: 700px;
        // .bg-image("../assets/set_user/pan","png");
        // background-size: 454px 122px;
        .role {
          width: calc(100% + 124px);
          // height: calc(100% - 85px);
          height: calc(100% - 61px);
          margin-right: -124px;
          margin-bottom: -61px;
          z-index: 2;
          > img {
            width: 560px;
            height: 506px;
          }
        }
        .pan {
          width: 100%;
          height: 122px;
          margin-bottom: 20px;
          // background: pink;
          // justify-self: flex-end;
          img {
            width: 454px;
            margin: 0 auto;
          }
        }
      }
      .name {
        width: 100%;
        height: 52px;
        text-align: center;
        padding-top: 300px;
        margin-bottom: 46px;
        img {
          // width: 248px;
          height: 52px;
        }
      }
    }
  }
  .bottom-section {
    position: absolute;
    bottom: 0;
    height: 288px;
    width: 100%;
    background-image: url(~@/assets/images/personalPage/bottom_bg@2x.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: 100% 100%;
    .head-box {
      padding: 0 60px 0 50px;
      margin-top: -140px;
      display: flex;
      justify-content: space-around;
      text-align: center;
      align-items: flex-end;
      .head-item {
        &.active {
          .head {
            width: 112px;
            height: 112px;
          }
        }
        .head {
          width: 92px;
          height: 92px;
        }
      }

      .text {
        color: #81a0b2;
        font-size: 28px;
      }
    }
    .welcome-box {
      padding: 44px 56px 20px 42px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .hd {
        .logo {
          width: 110px;
          height: 110px;
        }
      }
      .bd {
        font-size: 30px;
        line-height: 42px;
        .t {
          color: #fff;
        }
        .b {
          color: #fff3b2;
        }
      }
      .ft {
        .qrcode {
          width: 120px;
          height: 120px;
        }
      }
    }
    .btn-box {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      display: flex;
      justify-content: center;
      .btn {
        img {
          width: 396px;
          height: 112px;
        }
      }
    }
  }
}
</style>



